@import url('https://fonts.googleapis.com/css?family=Roboto:300,700');


.App {
  display: -ms-flexbox;
  display: flex;
  height: 100vh;
  width: auto;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-family: 'Roboto', sans-serif;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  
  
}
.title{
  text-align: center;
  padding: 0 100px;
}
.title h1{
  font-size: 5rem;
  margin-top:0;
  -webkit-text-shadow: 2px 7px 36px -2px rgba(0,0,0,0.64);
-moz-text-shadow: 2px 7px 36px -2px rgba(0,0,0,0.64);
text-shadow: 2px 7px 36px -2px rgba(0,0,0,0.64);
  

}
.title h3{
  font-size:2rem;
  margin-bottom: 10px;
  -webkit-text-shadow: 2px 7px 36px -2px rgba(0,0,0,0.64);
-moz-text-shadow: 2px 7px 36px -2px rgba(0,0,0,0.64);
text-shadow: 2px 7px 36px -2px rgba(0,0,0,0.64);

}



